.checkout__step4__btn {
    width: 300px;
    height: 40px;
    /* UI Properties */
    background: var(--primary-brand) 0% 0% no-repeat padding-box;
    background: #1363DF 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #39373729;
    border-radius: 10px;
    border-color: #1363DF;
    opacity: 1;
    color: #FFFFFF;
}

.checkout__step4__btn_div {
    margin-left: 12vw;
    margin-bottom: 35px;
}

.payment__info__border {
    border-bottom: #F4F5F5 2px solid;
}

.payment__opt__credit__card__div {
    font: normal normal normal 16px/24px Muli;
    margin-bottom: 24px;
}

.payment__option__div {
    font: normal normal normal 16px/24px Muli;
    border-top: #F4F5F5 2px solid;
    border-bottom: #F4F5F5 2px solid;
    padding-top: 15px;
    padding-bottom: 15px;
}

.helpcircle__div {
    margin-top: 2vw;
    width: 21px;
    height: 21px;
}

.check__square__img {
    margin-right: 16px;
    width: 24px;
    height: 24px;
}

.payment__product__cart__img {
    width: -webkit-fill-available;
    height: 120px;
    margin-right: 1vw;
}

.payment__product__cart__count {
    font: normal normal 600 16px/20px Muli;
    letter-spacing: 0px;
    color: #172026;
    opacity: 1;
    margin: 32px 0px 24px 0px;
}

.payent__product__summary__container {
    width: 45vw;
    height: 229px;
    border: #F4F5F5 2px solid;
    font: normal normal normal 16px/24px Muli;
    margin-bottom: 40px;
}

.payent__product__label {
    font: normal normal 600 16px/20px Muli;
    padding-bottom: 5px;
}

.payment__term__and__policy {
    text-decoration: underline;
    font: normal normal 600 16px/24px Muli;
    letter-spacing: 0px;
    color: #172026;
}

.payment__term__and__policy__label{
    margin-left: 6vw;
    margin-bottom: 120px;
}

.payent__product__sub__summary__container{
    margin-left: 32PX;
}