.shopping-cart-title-span {
    font: normal normal 600 40px/35px Source Serif Pro;
}

.shopping-cart-title {
    padding: 30px 35% 1% 35%;
}

.shopping-cart-sub-container {
    height: max-content;
    margin: 0vw 8vw 0vw 8vw;
}

.shopping__cart__border {
    margin-bottom: 40px;
    margin-left: 45%;
}

//mobile View
@media (min-width: 320px) and (max-width: 768px) {
    .shopping-cart-title {
        // padding: 0vw 0vw 0vw 24vw;
        // width: 100vw;
    }

    .shopping-cart-title-span {
        font: normal normal 600 7vw Source Serif Pro;
    }

    .shopping-cart-border {
        border: #1F39FF 2px solid;
        width: 15vw;
        height: 0px;
        margin-bottom: 8%;
        margin-left: 45vw;
    }

    .shopping-cart-sub-container {
        margin-right: 0px;
    }

}


@media (min-width: 1002px) and (max-width: 1920px) {
    .shopping-cart-title {
        padding: 30px 27% 1% 35%;
    }
}

@media (min-width: 768px) and (max-width: 1001px) {
    .shopping-cart-title {
        padding: 30px 10% 1% 27%;
    }
}