.product-detail-container {
    padding-top: 30px;
    margin: 0vw 10.5vw 0vw 15vw;

    font: normal normal normal 16px/24px Muli;
    letter-spacing: 0px;
    color: #172026;
    opacity: 1;
}

@media (min-width: 320px) and (max-width: 768px) {
    .product-detail-container {
        padding-top: 3vw;
        margin-left: 4vw;
        margin-right: 4vw;
    }

}