.homepage-container {
    width: auto;
    height: max-content;
    /* UI Properties */
    mix-blend-mode: multiply;
    font: normal normal normal 16px/24px Muli;
    opacity: 1;
}

.home-page-sub-container-1 {
    background: #E5E5E7;
}

.home-page-sub-container-2 {
    margin: 0vw 14vw 128px 15vw;
}

.man-grp-img {
    width: -webkit-fill-available;
    height: 650px;
}

.man-stand-img {
    width: -webkit-fill-available;
    height: 450px;
}

.man-sky-img {
    width: -webkit-fill-available;
    height: 400px;
    margin-top: 17px;
    margin-bottom: 15px;
}

.category-img {
    width: -webkit-fill-available;
    //width: 349px;
    height: 320px;
    margin-top: -35px;
}

.home-page-margin-botton {
    margin-bottom: 68px;
}

.block__details {
    width: -webkit-fill-available;
    height: 432px;
    /* UI Properties */
    background: #1B252C 0% 0% no-repeat padding-box;
    opacity: 1;
    color: #FFFFFF;
}

.back__arraow {
    width: 20px;
    height: 40px;
    margin-top: 304px;
    margin-left: 24px;
}

.home__title__div {
    font: normal normal 600 48px/60px Source Serif Pro;
    margin-top: 161px;
    margin-bottom: 17px;
}

.home__title__discription__div {
    font: normal normal normal 16px/23px Muli;
    margin-bottom: 17px;
}

.shop__now__btn {
    width: 220px;
    height: 40px;
    /* UI Properties */
    background: var(--primary-brand) 0% 0% no-repeat padding-box;
    background: #1363DF 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #39373729;
    border-radius: 10px;
    opacity: 1;
    border-color: #1363DF;
    font: normal normal bold 14px/16px Muli;
    letter-spacing: 0px;
    color: #FFFFFF;
    text-transform: uppercase;
    margin-top: 24px;
    margin-bottom: 67px;
}

.shop__cont__btn {
    width: 221px;
    height: 40px;
    background: var(--white) 0% 0% no-repeat padding-box;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 2px solid #2A65D7;
    border-radius: 10px;
    opacity: 1;
    font: normal normal bold 14px/16px Muli;
    letter-spacing: 0px;
    color: #1363DF;
    text-transform: uppercase;
    margin-top: 24px;
    margin-bottom: 67px;
}

.category__img__div {
    height: 90px;
    width: -webkit-fill-available;
    background-color: #1B252CD9;
    margin-right: 10px;
    margin-top: -60px;
}

.category__img__span {
    font: normal normal 600 24px/30px Source Serif Pro;
    color: #FFFFFF;
    margin-left: 15px;
}

.category__img__sub__span {
    color: #FFFFFF;
    font: normal normal normal 16px/23px Muli;
    letter-spacing: 0.32px;
    margin-left: 15px;
}

.home__title__1 {
    font: normal normal 600 48px/60px Source Serif Pro;
    margin-top: 47px;
    margin-bottom: 8px;
}

.home__title__1__div {
    margin-left: 7vw;
}

.home__subtitle__1 {
    font: normal normal 600 20px/25px Muli;
    margin-bottom: 16px;
}

.home__title__2 {
    font: normal normal 600 48px/60px Source Serif Pro;
    padding-bottom: 15px;
}

.home__title__2__div {
    padding: 82px 0px 0px 50px;
}

.shop__cont__service__btn {
    width: 180px;
    height: 40px;
    border: 2px solid var(--white);
    border: 2px solid #FFFFFF;
    border-radius: 10px;
    opacity: 1;
    color: #FFFFFF;
    background-color: #1B252C;
    margin-top: 32px;
}

.map__in__img {
    width: 33px;
    height: 40px;
    margin-left: -15px;
    margin-top: 12px;
}

.border {
    width: 102px;
    height: 8px;
    /* UI Properties */
    background: var(--primary-brand) 0% 0% no-repeat padding-box;
    background: #1363DF 0% 0% no-repeat padding-box;
    opacity: 1;
}

.border__div{
    margin-left: -3.5vw;
}